@font-face {
  font-family: "Helvetica";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Regular"), url("../fonts/HelveticaRegular.woff2") format("woff2"), url("../fonts/HelveticaRegular.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Medium"), url("../fonts/HelveticaMedium.woff2") format("woff2"), url("../fonts/HelveticaMedium.woff") format("woff");
}
@font-face {
  font-family: "Helvetica";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Helvetica Bold"), url("../fonts/HelveticaBold.woff2") format("woff2"), url("../fonts/HelveticaBold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
}
.container {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    margin: 0 auto;
    padding: 0 30px;
  }
}
@media (min-width: 1920px) {
  .container {
    width: 1220px;
    margin: 0 auto;
    padding: 0 40px;
  }
}
.page-header__wrapper {
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page-header__image {
  width: 77px;
  height: 24px;
}
.page-header__nav {
  padding: 16px 0;
}
.navigation {
  color: #000000;
}
.navigation__list {
  list-style: none;
  padding: 0;
}
.navigation__list {
  display: flex;
  justify-content: space-between;
}
.navigation__input-holder {
  display: none;
}
@media (min-width: 1920px) {
  .navigation__input-holder {
    display: block;
  }
}
.site-navigation {
  list-style: none;
  padding: 0;
}
.site-navigation__none {
  display: none;
}
.site-navigation__link {
  color: #9F9F9F;
  text-decoration: none;
}
.page-main__wrapper {
  padding: 0 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.page-main__holder {
  display: flex;
  align-items: flex-start;
  justify-self: right;
  text-align: right;
  margin: 0;
}
.page-main__box {
  padding: 16px 0;
}
.page-main__title {
  margin: 0;
  font-size: 30px;
  font-weight: bold;
  line-height: 34px;
}
.page-main__wrap {
  display: flex;
  gap: 5px;
  margin: 12px 0 0 0;
}
.page-main__text {
  font-size: 16px;
  font-weight: normal;
  margin: 0;
}
.page-main__link {
  color: #9F9F9F;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
}
.breadcrumb__holder {
  padding: 16px 0;
}
.breadcrumb__item {
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  color: #000000;
}
.breadcrumb__item-main {
  color: #9F9F9F;
}
.form {
  padding: 16px 0;
}
.form__label {
  color: #9F9F9F;
  font-size: 12px;
  line-height: 14px;
}
.form__item--address {
  display: grid;
}
.form__input {
  font-size: 16px;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  height: 50px;
  width: 285px;
  padding: 0;
}
.form__option {
  color: #9F9F9F;
  font-size: 12px;
  line-height: 14px;
}
.form__input::placeholder {
  align-items: flex-start;
}
.form__input--address::placeholder {
  color: #000000;
}
.cart {
  padding: 16px 0;
}
.cart__item {
  display: flex;
  gap: 12px;
  padding: 16px 0;
}
.cart__holder {
  display: flex;
  justify-content: space-between;
  gap: 32px;
}
.cart__title {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
}
.cart__text {
  font-size: 26px;
  font-weight: bold;
  line-height: 30px;
  margin: 14px  0;
}
.cart__slogan {
  display: none;
}
.cart__wrapper-input {
  position: relative;
  margin-top: 25px;
  margin-bottom: 0;
}
.cart__input {
  width: 81px;
  height: 32px;
  font-weight: bold;
  font-size: 14px;
  line-height: 26px;
  text-transform: uppercase;
  align-items: center;
  border: 1px solid #000000;
  outline: none;
  margin-top: 0px;
  margin-right: 0px;
  margin-bottom: 25px;
}
.cart__input::placeholder {
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  color: #000000;
}
.cart__button {
  position: absolute;
  border: none;
  background: none;
  outline: none;
  padding: 0;
}
.cart__button-reduce {
  width: 12px;
  height: 0;
  top: 8px;
  left: 5px;
}
.cart__button-increase {
  width: 12px;
  height: 12px;
  top: 10px;
  right: 10px;
}
.cart__product-price {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  text-align: right;
}
.cart__product-container {
  display: flex;
  flex-direction: column;
  justify-content: left;
}
.cart__button-delete {
  font-size: 12px;
  color: #9F9F9F;
  margin-top: 50px;
  text-align: right;
}
.cart__total-holder {
  display: flex;
  justify-content: space-between;
}
.cart__total-text {
  font-size: 26px;
  line-height: 30px;
  font-weight: bold;
}
.cart__button-link {
  width: 288px;
  height: 60px;
  background: none;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  border: 1px solid #000000;
}
